import React, { Component } from "react";
import { Link, withRouter } from "react-router-dom";
import { connect } from "react-redux";
import { Route, Redirect } from "react-router-dom";
import { logout } from "../redux/actions/auth/auth";
import { history } from "../history";

class Menu extends Component {
   constructor(props) {
      super(props);
      this.state = {
         userdata: this.props.userInfo,
      };
      console.log("UserData-------------", this.state.userdata);
   }
   handleitem = () => {
      this.props.logout();
      // console.log('LOGOUT',props.logout)
      history.push("/login");
   };

   render() {
      const currentTab = (history, path) => {
         // console.log(history.location.pathname);
         if (history.location.pathname === path) {
            return { color: "#2ecc72" };
         } else {
            return { color: "#FFFFFF" };
         }
      };
      return (
         <div>
            <ul className="nav nav-tabs bg-blue">
               {this.state.userdata && this.state.userdata.role === "user" && (
                  <li className="nav-item">
                     <Link
                        to="/user-dashboard"
                        style={currentTab(history, "/user-dashboard")}
                        className="nav-link"
                     >
                        User Dashboard
                     </Link>
                  </li>
               )}
               {this.state.userdata && this.state.userdata.role === "admin" && (
                  <li className="nav-item">
                     <Link
                        to="/user-dashboard"
                        className="nav-link"
                        style={currentTab(history, "/user-dashboard")}
                     >
                        Admin Dashboard
                     </Link>
                  </li>
               )}
               {/* {this.state.userdata.role &&
               this.state.userdata.role === "admin" ? (
                  <li className="nav-item">
                     <Link to="/user-dashboard" className="nav-link">
                        User Dashboard
                     </Link>
                  </li>
               ) : (
                  <Redirect to="/login" />
               )} */}
               {this.state.userdata && (
                  <li className="nav-item">
                     <Link
                        onClick={(e) => this.handleitem()}
                        className="nav-link"
                        style={{ marginLeft: "1050px" }}
                     >
                        Logout
                     </Link>
                  </li>
               )}
               {!this.state.userdata && <Redirect to="/login" />}
               {/* <li className="nav-item">
                  <Link
                     onClick={(e) => this.handleitem(e, "/login")}
                     className="nav-link"
                     style={{ marginLeft: "1050px" }}
                  >
                     Logout
                  </Link> */}

               {/* <Link
                     to="/basic-proj/login"
                     className="nav-link"
                     style={{ marginLeft: "1050px" }}
                  >
                     Login
                  </Link> */}
            </ul>
         </div>
      );
   }
}
const mapStateToProps = (state) => {
   return {
      userInfo: state.auth.login.userInfo,
   };
};
export default withRouter(connect(mapStateToProps, { logout })(Menu));
